.profile-container
{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: fit-content;
}

.profile-parent
{ 
    display: flex;
    align-items: center;
    color: aliceblue;
}

.profile-details
{
    margin-top: 80px;
    margin-left: 90px;
}

.profile-details-name
{
    font-size: 24px;
    font-family: 'Poppins SemiBold';
}

.profile-details-role
{
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
}

.profile-details-role h1
{
    font-size: 40px;
    font-family: cursive;
}

.primary-text
{
    color: (--white);
}

.highlighted-text
{
    color: var(--dark-orange);
}

.profile-role-tagline
{
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: 'Poppins Light';
}

.profile-options .highlighted-btn
{
    margin: 0 0 0 28px;
}

.profile-options .highlighted-btn:hover
{
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;
    border: 2px solid  var(--dark-orange);
}

.profile-picture
{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0 0 var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 30px 10px 0 160px;
}

.profile-picture-background
{
    height: 92%;
    width: 92%;
    background-image: url("../../../assets/Home/profile-picture.jpg");
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
}

.profile-picture-background:hover
{
    transform: scale(1.07);
    transition: 1s ease-out;
}

.colz .colz-icon i
{
    color: white;
    padding: 8px;
    padding-left: 0px;
}

.colz .colz-icon i:hover
{
    transform: scale(1.5);
}

@media screen and (max-width: 1110px) 
{
    .profile-picture
    {
        margin: 0 0 0 80px;
    }
    .profile-details-name
    {
        font-size: 20px;
    }
    .profile-details-role .primary-text
    {
        font-size: 12px;
    }
    .profile-options .highlighted-btn
    {
        width: 120px;
        padding: 11px 0;
        z-index: 700;
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width: 910px) 
{
    .profile-picture
    {
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
    .profile-options .highlighted-btn
    {
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width: 810px) 
{
    .profile-picture
    {
        margin: 0 !important;
    }
    .profile-parent
    {
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile-options
    {
        display: flex;
        justify-content: space-between;
    }
    .profile-details
    {
        margin: 25px 0 0 0;
    }
    .profile-options .highlighted-btn
    {
        margin-left: auto;
        margin-right: auto;
    }
    .profile-options,
    .profile-details-role
    {
        text-align: center;
    }
}

@media screen and (max-width: 375px) 
{
    .profile-picture
    {
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }
    .profile-details
    {
        width: 90%;
    }
    .profile-options .highlighted-btn
    {
        margin-left: auto;
        margin-right: auto;
    }
}