.contact{
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    align-items: center;
    justify-content: center;
    height: 20rem;
}
 .contact-container
 {
  background-color: var(--planCard);
 }

.join{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    height: 20rem;
}

.join>input{
    width: 20rem;
    height: 2rem;
    border: none;
    outline: none;
    border-bottom: 1px solid var(--lightgray);
    background-color: transparent;
    color: var(--lightgray);
}

.join>textarea{
    width: 20rem;
    height: 10rem;
    border: none;
    outline: none;
    border-bottom: 1px solid var(--lightgray);
    background-color: transparent;
    color: var(--lightgray);
}

*, *:before, *:after {
box-sizing: border-box;
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;
}

body, button, input {
font-family: 'Montserrat', sans-serif;
font-weight: 700;
letter-spacing: 1.4px;
}

.background {
display: flex;
min-height: 100vh;
}

.container {
flex: 1;
margin: auto;
padding: 10px;
}

.screen {
position: relative;
background: var(--planCard);
border-radius: 15px;
}

.screen:after {
content: '';
display: block;
position: absolute;
top: 0;
left: 20px;
right: 20px;
bottom: 0;
border-radius: 15px;
box-shadow: 0 20px 40px rgba(0, 0, 0, .4);
z-index: -1;
}

.screen-header {
display: flex;
align-items: center;
padding: 10px 20px;
background: #1f1f20;
border-top-left-radius: 15px;
border-top-right-radius: 15px;
}

.screen-header-left {
margin-right: auto;
}

.screen-header-button {
display: inline-block;
width: 8px;
height: 8px;
margin-right: 3px;
border-radius: 8px;
background: white;
}

.screen-header-button.close {
background: #ed1c6f;
}

.screen-header-button.maximize {
background: #e8e925;
}

.screen-header-button.minimize {
background: #74c54f;
}

.screen-header-right {
display: flex;
}

.screen-header-ellipsis {
width: 3px;
height: 3px;
margin-left: 2px;
border-radius: 8px;
background: #999;
}

.screen-body {
display: flex;
}

.screen-body-item {
flex: 1;
padding: 50px;
}

.screen-body-item.left {
display: flex;
flex-direction: column;
}

.app-title {
display: flex;
flex-direction: column;
position: relative;
color: #080808;
font-size: 26px;
}

.app-title:after {
content: '';
display: block;
position: absolute;
left: 0;
bottom: -10px;
width: 25px;
height: 4px;
background: #0f0e0f;
}

.app-contact {
margin-top: auto;
font-size: 8px;
color: #000000;
}

.app-form-button:hover {
color: #b9134f;
}

.credits {
display: flex;
justify-content: center;
align-items: center;
margin-top: 20px;
color: white;
font-family: 'Roboto Condensed', sans-serif;
font-size: 16px;
font-weight: normal;
}

.credits-link {
display: flex;
align-items: center;
color: #fff;
font-weight: bold;
text-decoration: none;
}

label {
  font-weight: bold;
}

input[type="text"],
input[type="email"],
textarea {
  width: 90%;
  padding: 5px;
  margin-top: 5px;
  margin-bottom: 10px;
  border: 2px solid #274b82;
  border-radius: 20px;
  text-align: center;
}

.msg
{
  font-weight: bold;
  height: 40px;
}

.cancel-button:hover {
  color: #a0391d;
}

.submit-button:hover {
  color: #0056b3;
}

@media screen and (max-width: 768px) {
.container{
  width: 100%;
  padding: 10px;
}

.screen-body {
  flex-direction: column;
}

.screen-body-item.left {
  margin-bottom: 30px;
}

.app-title {
  flex-direction: row;
}

.app-title span {
  margin-right: 12px;

}

.app-title:after {
  display: none;
}
}

@media screen and (max-width: 768px) {
.screen-body {
  padding: 40px;

}

.screen-body-item {
  padding: 0;
}
.btn submit-button,
.btn cancel-button {
  margin-left: auto;
  margin-right: auto;
}
}


